import React, { createContext, useState, useEffect, useCallback } from 'react';
import apiClient from '../services/apiClient';
import { toast } from '../components/common/Toast';

// 
export const AuthContext = createContext({
  isAuthenticated: false,
  user: null,
  token: null,
  loading: false,
  error: null,
  role: null,
  login: () => { },
  register: () => { },
  logout: () => { },
  clearError: () => { }
});

// 
export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [token, setToken] = useState(localStorage.getItem('token'));
  const [isAuthenticated, setIsAuthenticated] = useState(!!token);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [role, setRole] = useState(localStorage.getItem('userRole') || 'user');

  // 
  const clearError = () => setError(null);

  // 
  const setAuth = (userData, authToken, userRole) => {
    localStorage.setItem('token', authToken);
    if (userRole) {
      localStorage.setItem('userRole', userRole);
    }
    setToken(authToken);
    setUser(userData);
    setRole(userRole || 'user');
    setIsAuthenticated(true);
    setLoading(false);
    apiClient.setAuthToken(authToken);
  };

  // 
  const clearAuth = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('userRole');
    setToken(null);
    setUser(null);
    setRole('user');
    setIsAuthenticated(false);
    apiClient.setAuthToken(null);
  };

  // 
  const login = async (username, password) => {
    setLoading(true);
    setError(null);

    try {
      console.log('...');
      const response = await apiClient.login(username, password);
      console.log(':', response);

      if (response.success) {
        console.log('...');
        setAuth(response.user, response.token, response.user?.role);
        toast.success('');
        console.log('isAuthenticatedtrue');
        return true;
      } else {
        console.log(':', response.message);
        setError(response.message || '');
        setLoading(false);
        toast.error(response.message || '');
        return false;
      }
    } catch (err) {
      console.error(':', err);
      const errorMessage = err.response?.data?.message || err.message || '';
      setError(errorMessage);
      setLoading(false);
      toast.error(errorMessage);
      return false;
    }
  };

  // 
  const register = async (username, password, email) => {
    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.register(username, password, email);

      if (response.success) {
        setAuth(response.user, response.token, response.user?.role);
        toast.success('');
        return true;
      } else {
        setError(response.message || '');
        setLoading(false);
        toast.error(response.message || '');
        return false;
      }
    } catch (err) {
      const errorMessage = err.response?.data?.message || '';
      setError(errorMessage);
      setLoading(false);
      toast.error(errorMessage);
      return false;
    }
  };

  // 
  const logout = () => {
    clearAuth();
    toast.success('');
  };

  // token
  const checkAuth = useCallback(async () => {
    if (!token) return;

    try {
      // APItoken
      apiClient.setAuthToken(token);

      // 
      const userData = await apiClient.getUserInfo();

      if (userData.success) {
        setUser(userData.user);
        setRole(userData.user?.role || 'user');
        setIsAuthenticated(true);
      } else {
        // tokentoken
        try {
          const refreshResult = await apiClient.refreshToken();
          if (refreshResult.success) {
            setAuth(refreshResult.user, refreshResult.token, refreshResult.user?.role);
          } else {
            clearAuth();
          }
        } catch (refreshErr) {
          clearAuth();
        }
      }
    } catch (err) {
      clearAuth();
    }
  }, [token]);

  // 
  useEffect(() => {
    checkAuth();
  }, [checkAuth]);

  // 
  const authContextValue = {
    isAuthenticated,
    user,
    token,
    loading,
    error,
    role,
    login,
    register,
    logout,
    clearError
  };

  return (
    <AuthContext.Provider value={authContextValue}>
      {children}
    </AuthContext.Provider>
  );
};

export default AuthProvider; 